.sac-state-events-section {
    background-color: var(--ssgr);
    background-image: url('../svg/rivers-1600w.svg');
    background-repeat: no-repeat;
    position: relative;
    background-size: contain;
    background-blend-mode: screen;
    background-position: center;
    h2 {
        max-width: unset;
        text-align: center;
        color: var(--white);
        padding-bottom: 1rem;
    }
    .row {
        margin-right: 0;
        margin-left: 0;
        display: flex;
        justify-content: flex-end;
        ul.sac-state-events {
            padding: 0;
            width: 100%;
            margin: 0 auto; // max-width: 1140px;
            list-style: none;
            padding-left: 0;
            @media screen and (min-width: 500px) {
                display: flex;
                flex-direction: row;
                flex-wrap: wrap;
            }
            li {
                margin-bottom: 1rem;
                background-color: var(--wgolt);
                border: 2px solid var(--wgomd);
                border-radius: 4px;
                @media screen and (min-width: 500px) {
                    flex: 1 1 calc(50% - 1rem);
                    margin-right: 1rem;
                    &:nth-child(even) {
                        margin-right: 0;
                    }
                }
                @media screen and (min-width:1000px) {
                    flex: 1 1 calc(25% - 1rem);
                    &:nth-child(even) {
                        margin-right: 1rem;
                    }
                    &:nth-child(4n) {
                        margin-right: 0;
                    }
                }
                a {
                    display: block;
                    padding: 2rem;
                    text-decoration: none;
                    height: 100%;
                    line-height: 1.5rem;
                    background-size: 45%;
                    background-repeat: no-repeat;
                    background-position-x: 90%;
                    background-position-y: 80%;
                    &:link,
                    &:visited {
                        color: var(--black);
                        border: 2px solid var(--wgomd);
                    }
                    &:hover,
                    &:focus {
                        color: var(--ssgr);
                        border: 2px solid var(--wgolt);
                    }
                    .eventdate {
                        margin-bottom: .5rem;
                    }
                    .eventcontent {
                        font-size: 1.2rem;
                    }
                }
                &:hover,
                &:focus {
                    background-color: var(--wgomd);
                    border: 2px solid var(--wgolt);
                }
                &.academic {
                    a {
                        background-image: url(../svg/calendar-icons/wgo/a-academic.svg);
                    }
                }
                &.deadline {
                    a {
                        background-image: url(../svg/calendar-icons/wgo/b-deadline.svg);
                    }
                }
                &.student-activity,
                &.hornet-wow-event {
                    a {
                        background-image: url(../svg/calendar-icons/wgo/c-csuslogo.svg);
                    }
                }
                &.ceremony {
                    a {
                        background-image: url(../svg/calendar-icons/wgo/d-ceremony.svg);
                    }
                }
                &.meeting,
                &.networking,
                &.open-house {
                    a {
                        background-image: url(../svg/calendar-icons/wgo/e-networking.svg);
                    }
                }
                &.conference,
                &.presentation,
                &.lecture,
                &.training,
                &.workshop,
                &.professional-development {
                    a {
                        background-image: url(../svg/calendar-icons/wgo/f-presentation.svg);
                    }
                }
                &.concert,
                &.dance,
                &.movie,
                &.play,
                &.photography,
                &.performance,
                &.literature,
                &.exhibit {
                    a {
                        background-image: url(../svg/calendar-icons/wgo/g-performance.svg);
                    }
                }
                &.health-wellness,
                &.recreation {
                    a {
                        background-image: url(../svg/calendar-icons/wgo/h-health.svg);
                    }
                }
                &.sporting-event {
                    a {
                        background-image: url(../svg/calendar-icons/wgo/i-sports.svg);
                    }
                }
            }
        }
    }
    .cal-event-link {
        text-align: right;
        a {
            color: var(--white);
            &:hover,
            &:focus {
                color: var(--wgolt);
                text-decoration: none;
            }
        }
    }
}

[id^="spotlight"] + .sac-state-events-section {
    background-color: var(--wgo);
    background-image: url('../svg/rivers-1600w.svg');
    background-repeat: no-repeat;
    background-blend-mode: exclusion;
    background-size: contain;
    background-position: center;
    h2 {
        color: var(--wgodk);
    }
    .row {
        ul.sac-state-events {
            li {
                a {
                    &:link,
                    &:visited {
                        //                                                color: var(--black);
                        //                                                background: var(--wgolt);
                        //                                                border: 2px solid var(--wgomd);
                    }
                    &:hover,
                    &:focus {
                        color: var(--brzdk);
                        background-color: var(--wgomd);
                        border: 2px solid var(--wgolt);
                    }
                }
            }
        }
        .cal-event-link {
            text-align: right;
            a {
                color: var(--brzdk);
                &:hover,
                &:focus {
                    color: var(--brz);
                }
            }
        }
    }
}